    monospace;
}

body {
  box-sizing: border-box
}

*, *:before, *:after {
  box-sizing: inherit
} 

[tabindex]  {
  border: unset;
  outline: unset
}

.container, .game {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }
  @media (min-width: 574.8px) {
    .container, .game {
      max-width: 540px; } }
  @media (min-width: 767.8px) {
    .container, .game {
      max-width: 720px; } }
