  width: 500px;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
  text-align: center;
  border: 2px solid black; }

.cell {
  min-height: calc(500px / 9);
  position: relative;
  width: 11.11111%;
  font-size: 36px; }
  .cell .value {
    color: #1dbf56;
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: absolute; }
  .cell:hover:not(.cell.conflict) {
    background: #73ff9f7a; }
  .cell.fixed {
    font-weight: 400; }
    .cell.fixed .value {
      color: #223f3e; }
    .cell.fixed.conflict .value {
      color: #223f3e; }
  .cell.same {
    background: #73ff9f7a; }
  .cell.selected {
    background: #85ffab; }
    .cell.selected.conflict {
      background: #85ffab; }
  .cell.nearby {
    background: rgba(122, 195, 134, 0.35); }
  .cell.conflict {
    background: #ffbabacc; }
    .cell.conflict .value {
      color: #e91e63; }

.cell:not(:nth-child(9n + 10)):not(:first-child) {
  border-left: 1px solid #bec6d4; }

.cell:nth-child(1n + 28):not(:nth-child(1n + 37)) {
  border-top: 1.5px solid #34605E; }

.cell:nth-child(1n + 55):not(:nth-child(1n + 64)) {
  border-top: 1.5px solid #34605E; }

.cell:nth-child(3n + 3):not(:nth-child(9n + 9)) {
  border-right: 1.5px solid #34605E; }

.cell:not(:nth-child(1n + 73)) {
  border-bottom: 1px solid #bec6d4; }

.cell .notes {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  display: flex;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  position: absolute; }

.cell .note {
  width: 33.33333%;
  height: 33.33333%;
  justify-content: center;
  align-items: center;
  display: flex;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.4);
